import MataKuliahList from '@/app/components/MatKulList';
import { getAllMataKuliah } from '@/lib/data'; // asumsi ada fungsi ini

// Halaman ini adalah Server Component
export default async function MataKuliahPage() {
  // Ambil data mata kuliah di sisi server
  const semuaMataKuliah = await getAllMataKuliah();

  return (
    <main className="min-h-screen p-8 bg-gray-100">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-2xl font-bold mb-6">Mata Kuliah Semester Ini</h1>
        
        {/* Kirim data ke Client Component */}
        <MataKuliahList initialMataKuliah={semuaMataKuliah} />
      </div>
    </main>
  );
}
